import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { VscLoading } from "react-icons/vsc";

const NewsSectionBlock = styled.div`
    
    .row { padding-top:80px; display:flex; flex-wrap:wrap; }
    .loadWrap { min-height:500px; text-align:center; line-height:500px;
        .loadicon { font-size:100px;
            animation:loadspin infinite 2s linear
        }
        @keyframes loadspin {
            from { transform: rotate(0deg); }
            to { transfrom: rotate(1turn); }
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async() => {
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=6959cac198154161b4b280cfbde1113f`);
        console.log(response);
        
        setArticles(response.data.articles);
        setLoading(true);
        
    }
    useEffect(
        () => {
            getNews()
        }, [category]
    )

    if ( !loading ) {
        return <NewsSectionBlock>
            <div className="loadWrap">
                <div className="loadicon"><VscLoading /></div>
            </div>
            </NewsSectionBlock>
    }
    if ( !articles ) {
        return <NewsSectionBlock></NewsSectionBlock>;
    } else {
        return (
            <NewsSectionBlock>
                <div className="row">
                    {
                        articles.map( (article)=>(<NewsItem key={article.gugunNm} article={article} /> ) )
                    }
                </div>
            </NewsSectionBlock>
        );

    }
    
};

export default NewsSection;